import React from 'react'
import { Container } from 'reactstrap'
import axios from 'axios'
import { Check } from 'react-feather'
import DataTable from 'react-data-table-component'
import '../../../assets/scss/plugins/extensions/dropzone.scss'

const selectedStyle = {
  rows: {
    selectedHighlighStyle: {
      backgroundColor: 'rgba(115,103,240,.05)',
      color: '#7367F0 !important',
      boxShadow: '0 0 1px 0 #7367F0 !important',
      '&:hover': {
        transform: 'translateY(0px) !important'
      }
    }
  }
}
const user = JSON.parse(localStorage.getItem('logInUserData'))

class NotificationTab extends React.Component {
  state = {
    notificationList: [],
    columns: [
      {
        title: 'title',
        selector: 'title',
        sortable: false
      },
      {
        name: 'check',
        sortable: false,
        width: '50px',
        cell: (row) => (
          <div className="data-list-action">
            <Check
              className="cursor-pointer"
              size={window.screen.width < 600 ? 13 : 20}
              onClick={() => {
                this.markAsRead(row)
              }}
            />
          </div>
        )
      }
    ],
    user: user,
    mobile: user?.mobile,
    email: user?.email
  }

  componentDidMount = () => {
    let notifications = []
    let notification1 = {
      title: 'test title 1',
      status: 0,
      id: 'testid',
      description: 'THis is test description',
      createdOn: new Date()
    }
    notifications.push(notification1)
    let notification2 = {
      title: 'test title 1',
      status: 0,
      id: 'testid',
      description: 'THis is test description',
      createdOn: new Date()
    }
    notifications.push(notification2)
    let notification3 = {
      title: 'test title 1',
      status: 0,
      id: 'testid',
      description: 'THis is test description',
      createdOn: new Date()
    }
    notifications.push(notification3)

    this.setState({
      notificationList: notifications
    })
  }

  markAsRead = (data) => {
    if (data.id !== null) {
      let notifications = [...this.state.notificationList]
      let rowIndex = notifications.findIndex(
        (resource) => resource.key === data.id
      )
      notifications.splice(rowIndex, 1)
      this.setState({
        notificationList: notifications
      })
    }
  }

  postContactUsForm = (e) => {
    e.preventDefault()
    this.setState({ saveButtonText: 'Saving ...' })
    let { firstname, middlename, lastname, mobile } = this.state
    let user = JSON.parse(localStorage.getItem('logInUserData'))
    user.salutation = this.state.salutation
    user.name = firstname + ' ' + middlename + ' ' + lastname
    user.mobile = mobile
    localStorage.setItem('logInUserData', JSON.stringify(user))
    axios
      .post('/backendapi/adddetails', user,{headers: {'Authorization':'Bearer '+localStorage.getItem('authtoken')}})
      .then((res) => {
        window.location.reload()
      })
      .catch()
  }

  render() {
    let { columns, notificationList } = this.state
    return (
      <>
        <DataTable
          title="Updates"
          width="200"
          columns={columns}
          data={notificationList}
          noTableHead
          selectableRows={window.screen.width < 600 ? false : false}
          responsive
          pointerOnHover
          selectableRowsHighlight
          onSelectedRowsChange={(data) =>
            this.setState({ selected: data.selectedRows })
          }
          customStyles={selectedStyle}
        />
      </>
    )
  }
}
export default NotificationTab
